:root {
    --black: #150605;
    --red: #EA453C;
    --pink: #F47B6E;
    --light-blue: #5bd0ff;
    --blue: #00aeff;
    --dark-blue: #0070d8;
}

#store {
    margin: 1em;
    border-radius: 1em;
}

#store-cart, #store-title, .add-to-cart {
    color: var(--blue);
}

#store-cart:hover, #store-title:hover, .add-to-cart:hover {
    color: var(--dark-blue);
}

#store-cart:active, #store-title:active, .add-to-cart:active {
    color: var(--light-blue);
}

main#photos {
    padding: 1em;
    display: grid;
    grid-gap: 2px;
    grid-auto-rows: 100px;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-auto-flow: dense;
}

#cart-container {
    padding: 1em;
}

#cart-container .total-cost {
    text-align: right;
    font-size: 1.3em;
}

div#order-button > button {
    background-color: var(--blue);
    border: none;
    border-radius: .25em;
    color: white;
    cursor: pointer;
    padding: 15px;
    transition: all .2s ease-in-out;
}

div#order-button > button:focus {
    outline: none;
}
div#order-button > button:hover {
    background-color: var(--dark-blue);
}

.cart-item {
    margin: 20px 0;
}

.cart-item img {
    margin-right: auto;
    margin-left: 10px;   
}

img.image-grid {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.image-container {
    position: relative;
}

.favorite {
    color: var(--red);
    font-size: 1.2rem;
    position: absolute;
    top: 5px;
    left: 5px;
    cursor: pointer;
}

.ri-heart-fill {
    opacity: .7;
}

.ri-delete-bin-line, .ri-delete-bin-fill {
    font-size: 1.3em;
    cursor: pointer;
}

.cart {
    font-size: 1.2rem;
    position: absolute;
    top: 5px;
    right: 5px;
    cursor: pointer;
}
 
.wide {
    grid-column: span 2;
}

.tall {
    grid-row: span 2;
}

.big {
    grid-column: span 2;
    grid-row: span 2;
}

header {
    display: flex;
    justify-content: space-between;

    padding: 0 1.5em;
    border-radius: 1em 1em 0 0;
}

a {
    text-decoration: none;
}
