#dashboard {
    height: calc(100vh - var(--top-padding));
    width: 100%;
    background: no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    justify-content: space-between;
  }
  
  #top {
    padding: 1em;
  }
  
  #crypto-top {
    margin-bottom: .5em;
  }
  
  #crypto, #weather {
    opacity: .9;
    border-radius: .75em;
    padding: 1rem;
  }
  
  #dashboard-time {
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 2.5em;
    transform: translate(-50%, -50%);
  }
  
  #dashboard-time, #author {
    text-shadow: 1px 1px #555555;
  }
  
  #crypto-name {
    margin-left: 1rem;
    font-size: 2rem;   
  }
  
  .crypto-price {
    font-size: 1.3em;
  }
  
  .crypto-price > span {
    font-weight: bold;
  }
  
  #author {
    padding: 1em;
  }
  
  #weather {
    flex-wrap: wrap;
    width: 200px;
  }
  
  #weather > img {
    width: 70px;
  }
  
  #weather-temp {
    font-size: 2.5em;
  }
  
  #weather-city {
    width: 100%;
    text-align: center;
  }
  