
#navbar {
    position: fixed;
    z-index: 3;
    top: 0;
    left:0;
    right:0;
    border-radius: 0 0 .5rem .5rem;
  }

#navbar-list {
    height: 2em;
    margin: 0;
    list-style-type: none;
}

.navbar-link {
    text-decoration: none;
}

#toggle-theme {
    width: 75px;
    border: 1px solid black;
    border-radius: 1em;
    cursor: pointer;
}