body {
    overflow-x: hidden;
}

#start-screen {
    height: calc(100vh - var(--top-padding));
}

.circle {
    clip-path: circle(50%);
    height: 30em;
    width: 30em;
    position: absolute;
    z-index: 1;
}

.blue-circle {
    background: #456BD9;
    top: -15em;
    left: -5em;
}

.red-circle {
    background: #D94545;
    position: absolute;
    top: -20em;
    left: calc(100% - 10em);
}

.green-circle {
    background: #6CD945;
    height: 20em;
    width: 20em;
    top: 50%;
    left: 45%;
}

#quiz-title {
    z-index: 2;
}

#quiz-start {
    background: var(--darker-grey-blue);
    color: white;
    height: 50px;
    width: 150px;
    border-radius: .5em;
    font-size: .9em;
    margin-top: .5em;
    box-shadow: 1px 1px 1px black;
    border: 1px solid var(--black-blue);
    z-index: 2;
}

#quiz-submit {
    color: white;
    background-color: var(--darker-grey-blue);
    border: 1px solid var(--black-blue);
    border-radius: .5em;
    font-size: .9em;
    padding: 0;
    height: 50px;
    width: 50%;
}

.question {
    
    margin-top: 1.25em;
}

.question-title {
    margin: .5em;
    margin-bottom: 1.25em;
}

.question-answer {
    border: 1px solid var(--black-blue);
    border-radius: .5em;
    margin: .25em;
    padding: .25em;
}

.question-answer:active, .question-answer:focus,
#quiz-start:active, #quiz-start:focus,
.quiz-submit:active, .quiz-submit:focus {
    outline: none;
}


.question-answer:hover, #quiz-submit:hover, #quiz-start:hover {
    color: #101A35;
    background-color: white;
    border-color: #101A35;
    cursor: pointer;
}

.selected {
    color: #101A35;
    background-color: white;
    border-color: #101A35;
}

#results {
    margin: 1.5em;
}

.correct {
    background: orange;
    color: #101A35;
    border: none;
}

.non-interactive {
    cursor: default !important;
    pointer-events: none !important;
}

hr {
    margin-bottom: 0;
    margin-top: 1.25em;
    color: white;
    border-top: 1px solid white;
}