#landing-container {
    display: block;
    width: 30%;
    border-radius: .675em;
}

#landing-photo {
    border-radius: .675em .675em 0 0;
    width: 100%;
}

#info-name {
    margin-bottom: .25em;
    font-size: 2.2em;
}

#info-title {
    margin: 0;
    font-size: 1em;
    text-transform: uppercase;
    color: orange;
}

#info-link {
    margin: .8em; 
    font-size: .9em;
    letter-spacing: .1em;
}

#info-email {
    margin-top: 1em;
    width: 75%;
    max-width: 15em;
    padding: .5em;
    font-size: .85em;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

#info-mail {
    height:16px;
    width: 16px;
}

#about, #interests {
    margin-left: 12.5%;
    margin-right: 12.5%;
}

#about-title, #interests-title {
    margin-top: 3em;
    margin-bottom: 0;
}

#about-paragraph, #interests-paragraph {
    margin-top: .5em;
    line-height: 1.6em;
}

#interests-paragraph {
    margin-bottom: 3em;
}

@media (max-width: 750px) {
    #landing-container {
        width: 80%;
    }
}

@media (min-width: 750px) {
    #landing-container {
        width: 50%;
    }
}

@media (min-width: 1250px) {
    #landing {
        height: calc(100vh - var(--top-padding));
        padding: 6em;
    }
    #landing-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        width: 60%;
        padding: 1em;
    }

    #landing-photo {
        width: 45%;
        border-radius: 0.675em;
        margin: 1em;
    }

    #landing-content {
        width: 45%;
    }

    #social {
        width: 100%;
    }

    #about-title, #interests-title {
        margin-top: 2em;
    }

    #info-name {
        margin-top: 0;
    }

    #interests-paragraph {
        margin-bottom: 0;
    }
}

